body {
  margin: 0;
  font-family: "Ubuntu", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.container {
  text-align: center;
  height: 100%;
}
.quantity {
  display: flex;
  width: 100%;
  height: 25%;
  margin-top: 10px;
  justify-content: center;
}
.quantity_input {
  width: 35%;
  height: 25px;
  margin-left: 5px;
  margin-right: 5px;
  border-radius: 15px;
  border-color: black;
  text-align: center;
}
.quantity_minus {
  display: flex;
  background-color: #f93154;
  width: 35px;
  height: 30px;
  text-decoration: none;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  font-size: large;
  color: black;
  border: none;
  border-radius: 20px;
  border: 1px solid black;
}

.quantity_plus {
  display: flex;
  background-color: #00b74a;
  width: 35px;
  height: 30px;
  text-decoration: none;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 20px;
  border: 1px solid black;
}
